.profile__subtitle {
    margin: 16px 0 0;
    padding: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    max-width: 336px; 
}

@media screen and (max-width: 768px)  {
    .profile__subtitle {
        font-size: 14px;
        line-height: 17px;
        text-align: center;
        margin: 14px 0 0;
        max-width: 282px;
    }
}
